import React from 'react';
import { Box, Radio } from '@mui/material';

import useThemes from '../store/useThemes';

function Palette() {
  const { colorItems, color, onColor } = useThemes();

  return (
    <Box>
      {colorItems.map((item) => {
        return (
          <Radio key={item.id}
            value={item.name}
            checked={item.name === color.name}
            onChange={(event: any) => {
              onColor(event.target.value);
            }}
            sx={{
              color: item.value[400],
              '&.Mui-checked': {
                color: item.value[400],
              }
            }}
          />
        );
      })}
    </Box>
  );
}
export default React.memo(Palette);