import { create } from 'zustand';

interface TypesState {
  style: any,
  value: string | '';
  onKeydown: (event: any) => void;
  onKeyup: (event: any) => void;
  onChange: (event: any) => void;
}

const useEnter = create<TypesState>((set, get) => {
  const inputStyleReset = {
    all: 'unset',
    boxSizing: 'border-box',
    fontFamily: 'inherit',
    fontSize: 'inherit',
    color: 'inherit',
    width: '100%',
  };

  return {
    // input or textarea value
    style: inputStyleReset,
    value: '',
    onKeydown: (event: any) => {

    },
    onKeyup: (event: any) => {

    },
    onChange: (event: any) => {
      set({ value: event.target.value });
    },
  };
});

export default useEnter;
