import React from 'react';
import { ToggleButton, ToggleButtonGroup, useColorScheme } from '@mui/material';

import useThemes from '../store/useThemes';

function Themes() {
  const { mode, setMode } = useColorScheme();
  const { items } = useThemes();
  if (!mode) {
    return null;
  }
  return (
    <ToggleButtonGroup
      color="primary"
      size="small"
      exclusive
      value={mode}
      onChange={(_, value) => setMode(value)}
    >
      {items.map((item, index) => {
        const key = index;
        const Icon = item.icon;
        return (
          <ToggleButton key={key} value={item.value}
            sx={{ width: 'calc(100% / 3)' }}
          >
            <Icon />
          </ToggleButton>
        );
      })}
    </ToggleButtonGroup>
  );
}
export default Themes;