import { create } from 'zustand';
import { createTheme } from '@mui/material';
import { alpha } from '@mui/material/styles';
import { red, pink, purple, deepPurple, indigo, blue, lightBlue, cyan, teal, green, lightGreen, lime, yellow, amber, orange, deepOrange, brown, grey, blueGrey } from '@mui/material/colors';

import MonitorOutlinedIcon from '@mui/icons-material/MonitorOutlined';
import LightModeOutlinedIcon from '@mui/icons-material/LightModeOutlined';
import DarkModeOutlinedIcon from '@mui/icons-material/DarkModeOutlined';

interface ThemesState {
  items: any[];

  colorItems: any[];
  color: any;
  onColor: Function;

  decoration: {
    box: object;
    textShadow: Function;
  };
}

const useThemes = create<ThemesState>((set, get) => {
  const initialColor = () => {
    const x = window.localStorage.getItem('tpy-color-scheme');
    return x ? JSON.parse(x) : { id: 5, value: blue, name: 'blue' };
  };

  return {
    items: [
      { id: 0, value: 'system', label: 'system', icon: MonitorOutlinedIcon },
      { id: 1, value: 'light', label: 'light', icon: LightModeOutlinedIcon },
      { id: 2, value: 'dark', label: 'dark', icon: DarkModeOutlinedIcon },
    ],

    colorItems: [
      { id: 0, value: red, name: 'red' },
      { id: 1, value: pink, name: 'pink' },
      { id: 2, value: purple, name: 'purple' },
      { id: 3, value: deepPurple, name: 'deepPurple' },
      { id: 4, value: indigo, name: 'indigo' },
      { id: 5, value: blue, name: 'blue' },
      { id: 6, value: lightBlue, name: 'lightBlue' },
      { id: 7, value: cyan, name: 'cyan' },
      { id: 8, value: teal, name: 'teal' },
      { id: 9, value: green, name: 'green' },
      { id: 10, value: lightGreen, name: 'lightGreen' },
      { id: 11, value: lime, name: 'lime' },
      { id: 12, value: yellow, name: 'yellow' },
      { id: 13, value: amber, name: 'amber' },
      { id: 14, value: orange, name: 'orange' },
      { id: 15, value: deepOrange, name: 'deepOrange' },
      { id: 16, value: brown, name: 'brown' },
      { id: 17, value: grey, name: 'grey' },
      { id: 18, value: blueGrey, name: 'blueGrey' },
    ],
    color: initialColor(),
    onColor: (name: string) => {
      const { colorItems: items } = get();
      const color = items.find((item) => item.name === name);
      window.localStorage.setItem('tpy-color-scheme', JSON.stringify(color));
      set({ color });
    },

    decoration: {
      box: {
        p: 1,
        borderRadius: '20px !important',
        border: () => {
          const { color } = get();
          return `1px solid ${alpha(color.value[400], 0.2)}`;
        },
        boxShadow: 4,
        backdropFilter: 'blur(6px)', background: 'none !important',
        overflow: 'hidden',
      },
      textShadow: () => {
        const { color } = get();
        return `0px 0px 10px ${color.value[500]}`;
      },
    },
    onDecoration: (obj: object) => {
      const { decoration } = get();
      set({ decoration: { ...decoration, ...obj } })
    },
  }
});
export default useThemes;

export const theme = (color: any) => {
  const ct = createTheme({
    colorSchemes: { dark: true },
    palette: {
      primary: color,
      secondary: color,
    },
  });

  const updateShadows = (colors: any, shadows: string[]): string[] => 
    shadows.map((shadow) => shadow
      .replace(/rgba\(0,0,0,0\.2\)/, alpha(colors.dark, 0.4))
      .replace(/rgba\(0,0,0,0\.14\)/, alpha(colors.light, 0.3))
      .replace(/rgba\(0,0,0,0\.12\)/, alpha(colors.main, 0.2))
    );

  //  25    .
  const updatedShadows = updateShadows(ct.palette.primary, ct.shadows);
  const finalShadows = updatedShadows as [
    "none", string, string, string, string,
    string, string, string, string, string,
    string, string, string, string, string,
    string, string, string, string, string,
    string, string, string, string, string
  ];

  const background = {
    paper: alpha(ct.palette.background.paper, 0.7),
    default: alpha(ct.palette.background.default, 0.7),
  };

  return createTheme({
    colorSchemes: { dark: true },
    shadows: finalShadows,
    palette: {
      ...ct.palette,
      background,
    },
  });
};
