import React from 'react';
import { Navigate, Route, Routes } from 'react-router-dom';

import { CssBaseline } from '@mui/material';
import { ThemeProvider } from '@mui/material/styles';

import usePages from './store/usePages';
import useThemes, { theme } from './store/useThemes';

import Pages from './pages/Pages';

function App() {
  const { items: pageItems } = usePages();
  const { color } = useThemes();

  const themes = theme(color.value);

  return (
    <ThemeProvider theme={themes}>
      <CssBaseline />
      <Routes>
        {pageItems.map((
          { address, element: Element }, index
        ) => {
          if (!address) return null;
          return <Route key={index} path={address}
            element={Element ? <Pages children={<Element />} /> : null}
          />;
        })}
        <Route path="*" element={<Navigate to="/" />} />
      </Routes>
    </ThemeProvider>
  );
}

export default React.memo(App);
