import React from 'react';
import { Box, Drawer, IconButton, List } from '@mui/material';

import useNavigator from '../store/usePages';
import useThemes from '../store/useThemes';

import MenuIcon from '@mui/icons-material/Menu';
import NavigateIconButton from '../commons/NavigateIconButton';

function Navi() {
  const { menu, onMenu, items } = useNavigator();
  const { decoration } = useThemes();

  const open = Boolean(menu);
  const onClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    onMenu(event.currentTarget);
  };
  const onClose = () => {
    onMenu(null);
  };

  return (
    <React.Fragment>
      <IconButton
        id="navi-button"
        edge="start"
        color="inherit"
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        aria-controls={open ? 'navi-menu' : undefined}
        onClick={onClick}
        sx={{ mr: 2 }}
      >
        <MenuIcon />
      </IconButton>
      <Drawer open={open}
        onClose={onClose}
        sx={{
          '& .MuiBackdrop-root': { opacity: '0 !important' },
          '& .MuiDrawer-paper': { ...decoration.box, pt: 5, borderRadius: 0 },
        }}
      >
        <Box
          sx={{ width: 55 }}
          role="presentation"
          onClick={onClose}
        >
          <List component="nav">
            {items.map((
              { title, address, icon: IconEl }, index
            ) => index !== 0 && (
              <Box key={index}
                sx={{ display: 'flex', justifyContent: 'center', mt: 1, mb: 1 }}
              >
                <NavigateIconButton
                  title={title}
                  address={address}
                  icon={<IconEl />}
                  onParentsClose={onClose}
                />
              </Box>
            ))}
          </List>
        </Box>
      </Drawer>
    </React.Fragment>
  );
}
export default React.memo(Navi);