import { create } from 'zustand';

import CloseIcon from '@mui/icons-material/Close';
import HomeIcon from '@mui/icons-material/Home';
import PostAddIcon from '@mui/icons-material/PostAdd';
import SettingsIcon from '@mui/icons-material/Settings';

import Main from '../pages/Main';
import Lists from '../pages/Lists';
import Options from '../pages/Options';

interface PagesState {
  menu: any;
  onMenu: (menu: any) => void;

  items: {
    id: number;
    title: string;
    address: string | null;
    icon: React.FC;
    element: React.FC | null;
  }[];
}

const usePages = create<PagesState>((set) => ({
  menu: null,
  onMenu: (menu: any) => set(({ menu })),

  items: [
    { id: 0, title: 'close', address: null, icon: CloseIcon, element: null },
    { id: 1, title: '', address: '/', icon: HomeIcon, element: Main },
    { id: 2, title: 'list', address: '/list', icon: PostAddIcon, element: Lists },
    { id: 3, title: 'options', address: '/options', icon: SettingsIcon, element: Options },
  ],
}));

export default usePages;