import React from 'react';
import { useLocation } from 'react-router-dom';
import { AppBar, Toolbar, Typography } from '@mui/material';

import useThemes from '../store/useThemes';
import usePages from '../store/usePages';

import Navi from '../components/Navi';

function Header() {
  const location = useLocation();
  const { decoration } = useThemes();
  const { items } = usePages();

  return (
    <AppBar position="fixed"
      color="inherit"
      sx={{
        width: '100%',
        ...decoration.box,
        borderRadius: 0,
        p: 0,
      }}
    >
      <Toolbar variant="dense">
        <Navi />
        <Typography variant="h6" component="div" sx={{ flexGrow: 1, textShadow: decoration.textShadow(), }}>
          {items.find((item) => item.address === location.pathname)?.title || ''}
        </Typography>
      </Toolbar>
    </AppBar>
  );
}
export default React.memo(Header);