import React, { memo } from 'react';
import { useNavigate } from 'react-router-dom';

import { IconButton, Tooltip } from '@mui/material';

interface NavigateIconButtonProps {
  icon: React.ReactElement,
  title: string,
  address: string | null,
  onParentsClose?: () => void,
}

function NavigateIconButton({
  icon, title, address, onParentsClose,
}: NavigateIconButtonProps) {
  const navigate = useNavigate();

  const handleClick = React.useCallback(() => {
    if (onParentsClose) onParentsClose();
    if (address) {
      navigate(address);
    }
  }, [navigate, address, onParentsClose]);

  return (
    <Tooltip title={title} placement="right">
      <IconButton onClick={handleClick}
        sx={{
          bgcolor: 'background.paper',
          backdropFilter: 'blur(4px)',
          boxShadow: 2,
          '&:hover': {
            bgcolor: 'background.paper',
            backdropFilter: 'blur(4px)',
            boxShadow: 6,
          },
        }}
      >
        {icon}
      </IconButton>
    </Tooltip>
  );
}
export default memo(NavigateIconButton);