import React from 'react';
import { Accordion, AccordionDetails, AccordionSummary, Typography } from '@mui/material';

import useThemes from '../store/useThemes';

import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

function AccordionComponents({
  title,
  details,
}: {
  title: string;
  details: React.ReactNode;
}) {
  const { decoration } = useThemes();

  return (
    <Accordion defaultExpanded
      sx={{ ...decoration.box, p: 0, pb: 1, mb: 2 }}
    >
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
      >
        <Typography sx={{ textShadow: decoration.textShadow() }}>
          { title }
        </Typography>
      </AccordionSummary>
      <AccordionDetails>
        { details }
      </AccordionDetails>
    </Accordion>
  );
}
export default React.memo(AccordionComponents);